<template>
<div class="wrapper">
  <div class="products">
    <div class="product" v-for="product in rack" :key="product.id">
      <div class="info">
        <h1>{{product.name}}</h1>
      </div>
      <div class="image">
        <img :src="'/images/products/'+ product.image">
      </div>
      <div class="price">
        <h2>{{product.price}}</h2>
        <button @click="addToCart(product)">Add to Cart</button>
      </div>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'OutfitList',
  props: {
    rack: Array
  },
  methods: {
    addToCart(product) {
      this.$root.$data.cart.push(product);
    }
  }
}
</script>

