<template>
<div>
  <div class="pure-menu pure-menu-horizontal">
    <ul class="pure-menu-list">
      <li class="pure-menu-item"><a @click="select('Shirt')" href="#" class="pure-menu-link">Shirts</a></li>
      <li class="pure-menu-item"><a @click="select('Pants')" href="#" class="pure-menu-link">Pants</a></li>
      <li class="pure-menu-item"><a @click="select('Shoes')" href="#" class="pure-menu-link">Shoes</a></li>
      <li class="pure-menu-item"><a @click="select('Jacket')" href="#" class="pure-menu-link">Jackets</a></li>
    </ul>
  </div>
  <ProductList :products="products" />
</div>
</template>

<script>
import ProductList from "../components/ProductList.vue"
export default {
  name: 'Browse',
  components: {
    ProductList,
  },
  data() {
    return {
      type: '',
      id: '',
    }
  },
  computed: {
    products() {
      return this.$root.$data.products.filter(product => product.type === this.type);
    }
  },
  methods: {
    select(type) {
      this.type = type;
    },
    outfit() {
      let random = Math.floor(Math.random() * 5) + 1;
      this.id = random;
      console.log(random);
    }
  }
}
</script>
