<template>
  <div>
    <h2>Outfits: </h2>
    <div class="wrapper">
      <p v-show="this.$root.$data.rack.length === 0">Try creating a random outfit!</p>
    </div>
    <button @click="randomize()">Outfit</button>
    <OutfitList :rack="rack" />
  </div>
</template>

<script>
import OutfitList from "../components/OutfitList.vue"
export default {
  name: 'Outfit',
  components: {
    OutfitList
  },
  computed: {
    rack() {
      return this.$root.$data.rack;
    }
  },
  methods: {
    randomize() {
      let index = 0;
      for (let i = 0; i < 4; i++) {
        let rand = (Math.floor(Math.random() * 5));
        console.log(index + rand);
        var article = this.$root.$data.products[index + rand];
        this.$root.$data.rack.push(article);
        index += 5;
      }
    },
  }
}
</script>

<style scoped>

.wrapper, h2 {
  display: flex;
  align-items: center;
  justify-content: center;
}

.search {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 50%;
}

form {
  display: table;
  width: 100%;
}

i {
  display: table-cell;
  padding-left: 10px;
  width: 1px;
}

input {
  display: table-cell;
  font-size: 20px;
  border: none !important;
  box-shadow: none !important;
  width: 100%;
  height: 40px;
}
</style>
